import * as actionType from './actionType'
import { getData } from 'util'
import { API_BATCHORDERCONFIRMS_PAGE,

  API_BATCHPREPAREORDERCONFIRMS_LIST,
  API_BATCHPREPAREORDERCONFIRMS_UNCONFIRM_EXPORT,
  API_BATCHPREPAREORDERCONFIRMS_EXPORT_FAILED,
  API_RECORDFAILEDORDERCONFIRMS_CONFIRM,
  API_BATCHFAILEDORDERCONFIRMS_LIST
} from 'api'
//同步
export const update = object => ({
  type: actionType.BATCHORDERCONFIRM_UPDATE,
  data: object
})

// 获取批量工单确认列表
export const handleGetList = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHORDERCONFIRM_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_BATCHORDERCONFIRMS_PAGE, ...reqData })
    .then(data => {
      let { page, size } = reqData.searchParams
      callBack && callBack()
      dispatch({
        type: actionType.BATCHORDERCONFIRM_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
          // searchResult:
          // [
          //   {
          //     batchName: 'string',
          //     failed: 10,
          //     id: 1111,
          //     locationCode: 'string',
          //     operateTime: '2020-05-21T07:03:00.751Z',
          //     operator: 'string',
          //     prepared: 0,
          //     produceLine: 'string',
          //     produceTime: 'string',
          //     success: 20
          //   },
          //   {
          //     batchName: 'string',
          //     failed: 10,
          //     id: 2222,
          //     locationCode: 'string',
          //     operateTime: '2020-05-21T07:03:00.751Z',
          //     operator: 'string',
          //     prepared: 0,
          //     produceLine: 'string',
          //     produceTime: 'string',
          //     success: 20
          //   }
          // ]

        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHORDERCONFIRM_FAIL
      })
    })
}

// 获取批量工单确认-详情
export const getorderConfirmListOfTypes = (reqData, data) => (dispatch, getState) => {
  dispatch({
    type: actionType.BATCHORDERCONFIRM_UPDATE,
    data: { tableLoading_modal: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  // 批量工单确认待处理记录详情 -API_BATCHFAILEDORDERCONFIRMS_LIST
  // 批量工单确认失败记录详情 - API_BATCHFAILEDORDERCONFIRMS_LIST
  let api = searchParams.type === 'failed' ? API_BATCHFAILEDORDERCONFIRMS_LIST : API_BATCHPREPAREORDERCONFIRMS_LIST
  getData({ api, ...reqData })
    .then(data => {
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.BATCHORDERCONFIRM_UPDATE,
        data: {
          page,
          size,
          tableLoading_modal: false,
          searchParams_record: { ...searchParams },
          total: data.result.total,
          searchResultRecord: data.result || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHORDERCONFIRM_FAIL
      })
    })
}
// 批量确认失败的记录导出
export const handleExportFailedRecord = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHORDERCONFIRM_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_BATCHPREPAREORDERCONFIRMS_EXPORT_FAILED, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.BATCHORDERCONFIRM_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHORDERCONFIRM_FAIL

      })
    })
}
// 导出
export const handleExportUnConfirmOrder = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.BATCHORDERCONFIRM_UPDATE,
    data: { modalTuttonLoading_handleDownload: true }
  })
  getData({ api: API_BATCHPREPAREORDERCONFIRMS_UNCONFIRM_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.BATCHORDERCONFIRM_UPDATE,
        data: { modalTuttonLoading_handleDownload: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.BATCHORDERCONFIRM_FAIL
      })
    })
}
// 批量确认失败的记录
export const handleBatchConfirm = (reqData, callBack) => dispatch => {
  getData({ api: API_RECORDFAILEDORDERCONFIRMS_CONFIRM, ...reqData })
    .then(data => {
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.BOMSETTING_DET_FAIL
      })
    })
}



