import React, { PureComponent } from 'react'
import { Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
@connect(
  state => ({ ...state.batchOrderConfirm, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '工单编码',
        width: 100,
        dataIndex: 'orderCode',
        key: 'orderCode'
      },
      {
        title: '商品编码',
        width: 80,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品名称',
        width: 100,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '规格',
        width: 80,
        dataIndex: 'spec',
        key: 'spec'
      },
      {
        title: '单位',
        width: 60,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '计划产量',
        width: 60,
        dataIndex: 'planQty',
        key: 'planQty'
      },
      {
        title: '实际产量',
        width: 60,
        dataIndex: 'qty',
        key: 'qty'
      },
      {
        title: '生产日期',
        width: 100,
        dataIndex: 'produceTime',
        key: 'produceTime'
      },
      {
        title: '生产波次',
        width: 80,
        dataIndex: 'batchName',
        key: 'batchName'
      },
      {
        title: '产线',
        width: 100,
        dataIndex: 'produceLine',
        key: 'produceLine'
      },
      {
        title: '失败理由',
        width: 120,
        dataIndex: 'cause',
        key: 'cause'
      },
      {
        title: '处理时间',
        width: 100,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }
    ]
  }
  componentDidMount() {}
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { getorderConfirmListOfTypes, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      getorderConfirmListOfTypes({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams_record } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { getorderConfirmListOfTypes, searchParams_record, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        getorderConfirmListOfTypes({
          searchParams: {
            page,
            size,
            ...searchParams_record,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        getorderConfirmListOfTypes({
          searchParams: {
            page,
            size,
            ...searchParams_record,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams_record }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      getorderConfirmListOfTypes({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      tableLoading_modal,
      selectedRowKeys,
      searchResultRecord
    } = this.props
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading_modal}
          columns={this.columns}
          dataSource={searchResultRecord || []}
          scroll={{ x: countTableScroll(this.columns), y: 600 }}
          onChange={this.onChange}
          pagination={false}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



