import React, { PureComponent } from 'react'
import { Button, message } from 'antd'
import { API_BATCHORDERCONFIRMS_IMPORT } from 'api'
import { Import } from 'HOC'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  state => ({ ...state.batchOrderConfirm }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  /* 下载模版 */
  handleDownLoad = key => {
    const downloadData = JSON.parse(localStorage.getItem('b2bMmsDownload'))
    location.href = `${downloadData[key].downloadUrl}`
  }
  /* 导入成功 */
  handleImportSuccess = res => {
    const { handleGetList, searchParams, page, size, update } = this.props
    console.log('res is ', res)
    res && res.message === 'ok' && message.success('导入成功')
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  /* 报错导出 */
  handleExprotError = data => {
    const { handleGetList, searchParams, page, size, update } = this.props
    update({
      tableLoading: false,
      modalTuttonLoading: false
    })
    // 增加了部分成功导入的逻辑，导入失败之后刷新列表
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
    // 导出
  handleExport = () => {
    const { handleExportUnConfirmOrder, searchParams } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    const { produceTime = '', batchNumber = '', lineCode = '' } = reqObj
    const checkRequire = () => {
      let flag_produceTime = produceTime && produceTime.length > 0 ? true : false,
        flag_batchNumber = batchNumber && batchNumber.length > 0 ? true : false,
        flag_ilineCode = lineCode && lineCode.length > 0 ? true : false
      if (flag_produceTime && flag_batchNumber && flag_ilineCode) {
        delete reqObj.initTime
        delete reqObj.initDate
        return true
      } else {
        message.error('请选择【产线】【生产波次】【生产日期】后再执行导出操作 ')
        return false
      }
    }
    if (checkRequire()) {
      handleExportUnConfirmOrder(
          { searchParams: { produceTime, batchNumber, produceLineCode: lineCode } },
          () => {
            message.success('导出成功')
          }
        )
    }
  }
  render() {
    const {
      modalTuttonLoading = false,
      modalTuttonLoading_handleExport,
      modalTuttonLoading_handleDownload
    } = this.props
    return (
      <div className={style['table-tool']}>
        <Import
          multiple={true}
          action={{ api: API_BATCHORDERCONFIRMS_IMPORT }}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const { update } = this.props
            update({
              tableLoading: true,
              modalTuttonLoading: true
            })
          }}
          onResponse={data => {}}
        >
          <Button
            type='primary'
            icon='upload'
            className={style.btn}
            loading={modalTuttonLoading}
            disabled={modalTuttonLoading_handleExport}
          >
            批量导入
          </Button>
        </Import>
        {
          <Button
            type='primary'
            className={style.btn}
            onClick={this.handleExport}
            loading = {modalTuttonLoading_handleDownload}
          >
            导出未生产工单
          </Button>
        }
      </div>
    )
  }
}



