import React, { PureComponent } from 'react'
import { Table, message } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
import moment from 'moment'
let timer = null,
  firstLoad = true
@connect(
  state => ({ ...state.batchOrderConfirm, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '产线',
        width: 120,
        dataIndex: 'produceLine',
        key: 'produceLine',
        render: (text, record) => (
            <div>
              {text}
            </div>
        )
      },
      {
        title: '波次',
        width: 100,
        dataIndex: 'batchName',
        key: 'batchName'
      },
      {
        title: '生产日期',
        width: 100,
        dataIndex: 'produceTime',
        key: 'produceTime'
      },
      {
        title: '成功记录数',
        width: 100,
        dataIndex: 'success',
        key: 'success'
      },
      {
        title: '失败记录数',
        width: 100,
        dataIndex: 'failed',
        key: 'failed',
        render: (text, record) => (
          text ? <div>
          {this.renderModalBtn(text, record, 'failed')}
        </div> : text
      )
      },
      {
        title: '待处理记录数',
        width: 100,
        dataIndex: 'prepared',
        key: 'prepared',
        render: (text, record) => (
          text ? <div>
          {this.renderModalBtn(text, record, 'prepared')}
        </div> : text

      )
      },
      {
        title: '操作人',
        width: 100,
        dataIndex: 'operator',
        key: 'operator'
      },
      {
        title: '操作时间',
        width: 100,
        dataIndex: 'operateTime',
        key: 'operateTime'
      },
      {
        title: '操作',
        width: 150,
        dataIndex: 'xxxx',
        key: 'xxx',
        render: (text, record) => (
            <div>
              {this.renderDetail(text, record)}
            </div>
          )
      }
    ]
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, searchResult = [] } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams,
      produceTime: moment().format('YYYY-MM-DD') } }, () => {
        // 列表中有待处理的工单  5秒钟查询一次最新的数据
      timer = setInterval(() => {
        console.log('searchResult is', searchResult)
        console.log('this.props is', this.props.searchResult)
        let flag = false
        this.props.searchResult && Array.isArray(this.props.searchResult) && this.props.searchResult.map((item) => {
          if (Number(item.prepared) > 0) {
            console.log('item.prepared is', item.prepared)
            flag = true
          }
        })
        console.log()
        if (flag)
          handleGetList({ searchParams: { page, size, ...searchParams,
            produceTime: moment().format('YYYY-MM-DD') } }, () => {
            firstLoad = false
          })
      }, 5000)
      firstLoad = false
    })
  }
  componentWillUnMount() {
    clearInterval(timer)
    firstLoad = null
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 失败记录查询 - failed  待处理记录查询 -  prepared
  renderModalBtn(text, record, type) {
    let dom = <div
          className={style.link_in_table}
          onClick={this.showModalAction.bind(this, record, type)}
        > {text}
        </div>
    return dom
  }
  showModalAction = (record, type) => {
    const { update, getorderConfirmListOfTypes } = this.props
    const { id = '' } = record
    // 弹框与弹框控制参数的对应关系
    let typeNum = {
      failed: 'isShowModalFailRecord',
      prepared: 'isShowModalWaitOpertaeRecord'
    }
    if (id) {
      //获取详情
      const params = {
        searchParams: {
          batchId: id,
          type
        }
      }
      getorderConfirmListOfTypes(params)
    }
    update({
      [typeNum[type]]: true
    })
  }
  // 详情按钮
  renderDetail(text, record, index) {
    let dom = <div
          className={style.link_in_table}
          onClick={this.linkToDet.bind(this, record)}
        >
          {'详情'}
        </div>
    return dom
  }

  linkToDet = (record) => {
    const { history } = this.props
    const { id = '' } = record
    return history.push(
      `/mms/productCenter/batchOrderConfirm/det/${id}`)
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const { total, page, size, tableLoading, searchResult = [] } = this.props
    console.log(' this.props11111', this.props)
    let pagination = {
      total,
      showTotal: total => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          // rowSelection={rowSelection}
        />
      </div>
    )
  }
}



