import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker, Checkbox, Input } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import {
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST,
  API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST
} from 'api'
import moment from 'moment'
const FormItem = Form.Item
const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 }
  }
}
@connect(
  state => ({ ...state.batchOrderConfirm }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      checked: false
    }
  }
  componentDidMount() {
    const { searchParams, update } = this.props
    update(
      {
        searchParams: {
          ...searchParams,
          initDate: moment(),
          produceTime: moment().format('YYYY-MM-DD')
        }
      }
    )
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        console.log('reqObj[i] is', reqObj[i])
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initDate
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }
  onChangeFailed = e => {
    console.log('checked = ', e.target.checked)
    this.onInputChange({ onlyFailed: e.target.checked })
  }
  // 重置
  reset = () => {
    const { update, form } = this.props
    form.setFieldsValue({ initDate: moment() })
    update({ searchParams: {
      initDate: moment(),
      produceTime: moment().format('YYYY-MM-DD')
    } })
  }
  render() {
    const {
      searchParams = {},
      form: { getFieldDecorator }
    } = this.props
    const { lineCode, checked, batchNumber, initDate, operator = '', onlyFailed = false } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
              label='产线'
              allowClear={true}
              selectItem={{
                data: lineCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ lineCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              dateApi={API_PRODUCTIONFREQUENCYS_BATCHTYPE_LIST}
              label='生产波次'
              allowClear={true}
              selectItem={{
                data: batchNumber,
                key: 'code',
                valueKey: ['name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ batchNumber: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className={'commonInput'}>
              {/* <label>生产日期：</label> */}
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='生产日期'>
                  {getFieldDecorator('initDate', {
                    initialValue: initDate,
                    rules: [
                    ]
                  })(
                    <DatePicker
                      allowClear={false}
                      onChange={time => {
                        let produceTime =
                          time && `${moment(time).format('YYYY-MM-DD')}`
                        this.onInputChange({
                          produceTime,
                          initDate: time
                        })
                      }}
                      format='YYYY-MM-DD'
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput '>
              <label htmlFor=''>操作人：</label>
              <div className='box '>
                <Input
                  value={operator}
                  onChange={e => {
                    const {
                      target: { value = '' }
                    } = e
                    this.onInputChange({ operator: value })
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <div className='box' style={{ marginRight: 0, marginLeft: '46px' }}>
                <Checkbox
                  checked={onlyFailed}
                  // disabled={this.state.disabled}
                  onChange={this.onChangeFailed}
                >
                  {'失败记录查询'}
                </Checkbox>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type='primary' onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



