import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'
import OperationModalFailRecord from './components/./OperationModalFailRecord'
import OperationModalWaitOpertaeRecord from './components/OperationModalWaitOpertaeRecord'

@connect(
  state => ({
    ...state.batchOrderConfirm,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ searchResult: [], searchParams: {}, searchResultRecord: [] })
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['生产中心', '批量工单确认']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Tool menuPermissionIds={menuPermissionIds} history={history} />
        <Table menuPermissionIds={menuPermissionIds} history={history} />
        {this.props.isShowModalFailRecord ? <OperationModalFailRecord /> : null}
        {this.props.isShowModalWaitOpertaeRecord ? <OperationModalWaitOpertaeRecord menuPermissionIds={menuPermissionIds}/> : null}
      </section>
    )
  }
}



