import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import {} from 'api'
const FormItem = Form.Item
@connect(
  state => ({ ...state.batchOrderConfirm }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 查询数据
  query = () => {
    const {
      getorderConfirmListOfTypes,
      size,
      form,
      searchParams_record
    } = this.props
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      let { orderCode = '' } = reqObj
      if (!err) {
        console.log('reqObj is', reqObj)
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        getorderConfirmListOfTypes({
          searchParams: { ...reqObj, ...searchParams_record, orderCode }
        })
      }
    })
  }

  // 重置
  reset = () => {
    const { form } = this.props
    form.setFieldsValue({ orderCode: '' })
  }
  render() {
    const {
      form: { getFieldDecorator },
      modalTuttonLoading
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <FormItem {...formItemLayout}>
              {getFieldDecorator('orderCode', {
                // rules: [
                //   // {
                //   //   required: true,
                //   //   message: '请填写!'
                //   // }
                // ]
              })(
                <div className='commonInput '>
                  <label htmlFor=''>工单编码：</label>
                  <div className='box '>
                    <Input onChange={e => {}} />
                  </div>
                </div>
              )}
            </FormItem>
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



