import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, Input, message, Modal } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import {} from 'api'
const FormItem = Form.Item
import Debounce from 'lodash-decorators/debounce'
import _ from 'lodash'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.batchOrderConfirm }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 查询数据
  @Debounce(600)
  query = () => {
    console.log(_, 3333)
    console.log(Debounce, 44444)
    const { getorderConfirmListOfTypes, size, form, searchParams_record } = this.props
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      const { orderCode = '' } = reqObj
      if (!err) {
        console.log('reqObj is', reqObj)
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        getorderConfirmListOfTypes({ searchParams: { ...reqObj, ...searchParams_record, orderCode } })
      }
    })
  }

  // 重置
  reset = () => {
    const { form } = this.props
    form.setFieldsValue({ orderCode: '' })
  }
  // 批量确认
  handleOperator = () => {
    const {
      getorderConfirmListOfTypes,
      searchParams_record,
      searchParams,
      page,
      size,
      handleGetList,
      selectedRowKeys,
      handleBatchConfirm,
      update
    } = this.props
    if (!(selectedRowKeys && selectedRowKeys.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    console.log('searchParams_record is', searchParams_record)
    confirm({
      title: '温馨提示',
      content: '是否进行批量确认操作？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        console.log(22222, selectedRowKeys)
        handleBatchConfirm(
          {
            data: selectedRowKeys
          },
          () => {
            update({ selectedRowKeys: [], isShowModalFailRecord: false })
            message.success('操作成功')
            getorderConfirmListOfTypes({ searchParams: { ...searchParams_record } })
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  // 导出
  handleExport = () => {
    const { handleExportFailedRecord, searchParams_record, page, size, searchResultRecord } = this.props
    if (!(searchResultRecord && Array.isArray(searchResultRecord) && searchResultRecord.length > 0)) {
      return message.error('没有可以操作的选项')
    }
    let reqObj = { ...searchParams_record }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    console.log('searchParams_record is', searchParams_record)
    handleExportFailedRecord({ searchParams: { ...reqObj } }, () => {
      message.success('导出成功')
    })
  }
  render() {
    const {
      menuPermissionIds,
      modalTuttonLoading,
      searchResultRecord,
      searchParams_record,
      update,
      form: { getFieldDecorator }
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <FormItem {...formItemLayout}>
              {getFieldDecorator('orderCode', {
                // rules: [
                //   // {
                //   //   required: true,
                //   //   message: '请填写!'
                //   // }
                // ]
              })(
                <div className='commonInput '>
                  <label htmlFor=''>工单编码：</label>
                  <div className='box '>
                    <Input onChange={e => {
                      console.log(e, 3333)
                      update({
                        searchParams_record: {
                          ...searchParams_record,
                          orderCode: e.target.value || ''
                        }
                      })
                    }} />
                  </div>
                </div>
              )}
            </FormItem>
          </Col>
          <Col span={8} push={8}>
            <Row type='flex' justify='end'>
              <Button
                type='primary'
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type='primary'
                className={`${style.mr10} ${style.btn}`}
                onClick={this.handleOperator}
              >
                批量确认
              </Button>
              <Button
                type='primary'
                className={style.btn}
                onClick={this.handleExport}
                disabled={!(searchResultRecord && Array.isArray(searchResultRecord) && searchResultRecord.length > 0)}
                loading={modalTuttonLoading}
              >
                导出
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



